.App {
  text-align: center;
  /* background-image: url(./back2.jpg); */
  overflow: hidden;
}


.App-pers {
  height: 28vmin;
  border-radius: 15%;
  pointer-events: none;
  margin-bottom: 15px;
}

.app-name {
  color:rgb(26, 26, 92);
}

.App-header {
  min-height: 45vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: "#A7A7A7";
}

.App-desc {
  color:rgb(4, 0, 58);
  font-size: calc(9px + 1vmin);
}

::-webkit-scrollbar {
  display: none;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
